.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.heroProb {
  position: absolute;
  margin: 0em 1em 3.5em 0.5em;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.heroIconGrid {
  flex-basis: 0 !important;
}

.heroIcon {
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid white;
  max-height: 7em;
  max-width: 7em;
  width: 7em;
  height: 7em;
  min-height: 7em;
  min-width: 7em;
  /*height: 80px;
  width: 80px;*/
}

.closeIcon {
  max-height: 1em;
  max-width: 1em;
  width: 1em;
  height: 1em;
  min-height: 1em;
  min-width: 1em;
  margin-left: 0.2em;
  /*height: 80px;
  width: 80px;*/
}

.heroIconSpan {
  width: 100%;
  text-align: center;
}